@import "https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap";
@import "https://fonts.googleapis.com/css2?family=Clicker+Script&display=swap";
:root {
  --bg-black-900: #f2f2fc;
  --bg-black-100: #fdf9ff;
  --bg-black-50: #e8dfec;
  --text-black-900: #302e4d;
  --text-black-700: #504e70;
}

body.dark {
  --bg-black-900: #151515;
  --bg-black-100: #222;
  --bg-black-50: #393939;
  --text-black-900: #fff;
  --text-black-700: #e9e9e9;
}

body {
  font-size: 16px;
  line-height: 1.5;
  font-family: "Poppins" sans-serif;
}

* {
  box-sizing: border-box;
  outline: none;
  margin: 0;
  padding: 0;
  text-decoration: none;
}

:before, :after {
  box-sizing: border-box;
}

ul {
  list-style: none;
}

.section {
  background: var(--bg-black-900);
  opacity: 1;
  min-height: 100vh;
  padding: 0 30px;
  display: block;
  top: 0;
}

.hidden {
  display: none !important;
}

.main-content {
  padding-left: 270px;
}

.nav li a.active, .nav li a:hover {
  color: var(--skin-color);
}

.padd-15 {
  padding: 10px;
}

.container {
  width: 100%;
  max-width: 1100px;
  margin: auto;
}

.section .container {
  padding-top: 60px;
  padding-bottom: 70px;
}

.section-title {
  flex: 0 0 100%;
  max-width: 100%;
  margin-bottom: 60px;
}

.section-title h2 {
  color: var(--text-black-900);
  font-size: 40px;
  font-weight: 700;
  position: relative;
}

.section-title h2:before {
  content: "";
  background-color: var(--skin-color);
  width: 50px;
  height: 4px;
  position: absolute;
  top: 100%;
  left: 0;
}

.section-title h2:after {
  content: "";
  background-color: var(--skin-color);
  width: 25px;
  height: 4px;
  margin-top: 8px;
  position: absolute;
  top: 100%;
  left: 0;
}

.row {
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  display: flex;
  position: relative;
}

.btn {
  color: #fff;
  white-space: nowrap;
  background: var(--skin-color);
  border: none;
  border-radius: 40px;
  padding: 12px 35px;
  font-size: 16px;
  font-weight: 500;
  transition: all .3s;
  display: inline-block;
}

.btn:hover {
  transform: scale(1.05);
}

.shadow-dark {
  box-shadow: 0 0 20px #302e4d26;
}

.aside {
  background: var(--bg-black-100);
  border-right: 1px solid var(--bg-black-50);
  z-index: 10;
  justify-content: center;
  align-items: center;
  width: 270px;
  height: 100%;
  padding: 30px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.aside .logo {
  text-transform: capitalize;
  font-size: 30px;
  position: absolute;
  top: 50px;
}

.aside .logo a {
  color: var(--text-black-900);
  letter-spacing: 5px;
  padding: 15px 20px;
  font-size: 30px;
  font-weight: 700;
  position: relative;
}

.aside .logo a span {
  font-family: Clicker Script, cursive;
  font-size: 40px;
}

.aside .logo a:before {
  content: "";
  border-bottom: 5px solid var(--skin-color);
  border-left: 5px solid var(--skin-color);
  width: 20px;
  height: 20px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.aside .logo a:after {
  content: "";
  border-top: 5px solid var(--skin-color);
  border-right: 5px solid var(--skin-color);
  width: 20px;
  height: 20px;
  position: absolute;
  top: 0;
  right: 0;
}

.aside .nav-toggler {
  border: 1px solid var(--bg-black-50);
  cursor: pointer;
  background: var(--bg-black-100);
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 45px;
  height: 40px;
  display: none;
  position: fixed;
  top: 20px;
  left: 300px;
}

.aside .nav-toggler span {
  background: var(--skin-color);
  width: 18px;
  height: 2px;
  display: inline-block;
  position: relative;
}

.aside .nav-toggler span:before {
  content: "";
  background: var(--skin-color);
  width: 18px;
  height: 2px;
  position: absolute;
  top: -6px;
  left: 0;
}

.aside .nav-toggler span:after {
  content: "";
  background: var(--skin-color);
  width: 18px;
  height: 2px;
  position: absolute;
  top: 6px;
  left: 0;
}

.aside .nav {
  margin-top: 50px;
}

.aside .nav li {
  margin-bottom: 20px;
  display: block;
}

.aside .nav li a {
  border-bottom: 1px solid var(--bg-black-50);
  color: var(--text-black-900);
  padding: 5px 15px;
  font-size: 16px;
  font-weight: 600;
  display: block;
}

.aside .nav li a.active {
  color: var(--skin-color);
}

.aside .nav li a i {
  margin-right: 15px;
}

.home {
  min-height: 100vh;
  color: var(--text-black-900);
  display: flex;
}

.home .home-info {
  flex: 0 0 60%;
  max-width: 60%;
}

h3.hello {
  margin: 15px 0;
  font-size: 28px;
}

h3.hello span {
  color: var(--skin-color);
  font-family: Clicker Script, cursive;
  font-size: 30px;
  font-weight: 700;
}

h3.my-profession {
  margin: 15px 0;
  font-size: 30px;
}

.typing {
  color: var(--skin-color);
}

.home-info p {
  color: var(--text-black-700);
  margin-bottom: 70px;
  font-size: 20px;
}

.home .home-img {
  text-align: center;
  flex: 0 0 40%;
  max-width: 40%;
  position: relative;
}

.home-img:after {
  content: "";
  border-bottom: 10px solid var(--skin-color);
  border-right: 10px solid var(--skin-color);
  width: 80px;
  height: 80px;
  position: absolute;
  bottom: -40px;
  right: -15px;
}

.home-img:before {
  content: "";
  border-top: 10px solid var(--skin-color);
  border-left: 10px solid var(--skin-color);
  width: 80px;
  height: 80px;
  position: absolute;
  top: -40px;
  left: -20px;
}

.home .home-img img {
  border-radius: 5px;
  height: 400px;
  margin: auto;
}

.about .about-content, .about .about-content .about-text {
  flex: 0 0 100%;
  max-width: 100%;
}

.about .about-content .about-text h3 {
  color: var(--text-black-900);
  margin-bottom: 15px;
  font-size: 24px;
  font-weight: 700;
}

.about .about-content .about-text h3 span {
  color: var(--skin-color);
}

.about .about-content .about-text p {
  color: var(--text-black-700);
  font-size: 16px;
  line-height: 25px;
}

.about .about-content .personal-info {
  flex: 0 0 60%;
  max-width: 60%;
  margin-top: 40px;
}

.about .about-content .personal-info .info-item {
  flex: 0 0 50%;
  max-width: 50%;
}

.about .about-content .personal-info .info-item p {
  color: var(--text-black-900);
  border-bottom: 1px solid var(--bg-black-50);
  padding: -25px 0;
  font-size: 16px;
  font-weight: 600;
}

.about .about-content .personal-info .info-item p span {
  color: var(--text-black-700);
  margin-left: 4px;
  font-weight: 400;
  display: inline-block;
}

.about .about-content .personal-info .buttons {
  margin-top: 30px;
}

.about .about-content .personal-info .buttons .btn {
  margin-top: 10px;
  margin-right: 15px;
}

.about .about-content .skills {
  flex: 0 0 40%;
  max-width: 40%;
  margin-top: 40px;
}

.about .about-content .skills .scroll-container .skill-item {
  flex: 0 0 100%;
  max-width: 100%;
}

.about .about-content .skills .scroll-container .skill-item h5 {
  color: var(--text-black-900);
  text-transform: capitalize;
  font-size: 16px;
  font-weight: 600;
  line-height: 40px;
}

.about .about-content .skills .scroll-container .skill-item .progress {
  background-color: var(--bg-black-50);
  border-radius: 4px;
  width: 100%;
  height: 7px;
  position: relative;
}

.about .about-content .skills .scroll-container .skill-item {
  margin-bottom: -5px;
}

.about .about-content .skills .scroll-container .skill-item .progress .progress-in {
  background-color: var(--skin-color);
  border-radius: 4px;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.about .about-content .skills .scroll-container {
  border: 1px solid #ccc;
  max-height: 400px;
  padding: 0 15px;
  overflow-y: scroll;
}

.about .about-content .skills .scroll-container .skill-item .skill-percent {
  color: var(--text-black-900);
  font-weight: 400;
  line-height: 40px;
  position: absolute;
  top: -40px;
  right: 0;
}

.about .about-content .education, .about .about-content .experience {
  flex: 0 0 50%;
  max-width: 50%;
  margin-top: 30px;
}

.about .about-content h3.title {
  color: var(--text-black-900);
  margin-bottom: 30px;
  font-size: 24px;
  font-weight: 700;
}

.about .about-content .timeline-box {
  flex: 0 0 100%;
  max-width: 100%;
}

.about .about-content .timeline {
  background-color: var(--bg-black-100);
  border: 1px solid var(--bg-black-50);
  border-radius: 10px;
  width: 100%;
  padding: 30px 15px;
  position: relative;
}

.about .about-content .timeline .timeline-item {
  padding-bottom: 50px;
  padding-left: 37px;
  position: relative;
}

.about .about-content .timeline .timeline-item:last-child {
  padding-bottom: 0;
}

.about .about-content .timeline .timeline-item:before {
  content: "";
  background-color: var(--skin-color);
  width: 2px;
  height: 100%;
  position: absolute;
  top: 0;
  left: 7px;
}

.about .about-content .timeline .circle-dot {
  background-color: var(--skin-color);
  border-radius: 50%;
  width: 15px;
  height: 15px;
  position: absolute;
  top: 0;
  left: 0;
}

.about .about-content .timeline .timeline-date {
  color: var(--skin-color);
  margin-bottom: 10px;
  font-size: 16px;
  font-weight: 900;
}

.about .about-content .timeline .timeline-date .fa {
  margin-right: 5px;
}

.about .about-content .timeline .timeline-title {
  text-transform: capitalize;
  color: var(--text-black-900);
  margin-bottom: 15px;
  font-size: 18px;
  font-weight: 700;
}

.about .about-content .timeline .timeline-text {
  text-align: justify;
  color: var(--text-black-700);
  font-size: 16px;
  line-height: 25px;
}

.about .about-content .education .timeline-box .timeline, .about .about-content .experience .timeline-box .timeline {
  flex: 0 0 50%;
  max-width: 100%;
  max-height: 100vh;
  margin-top: 30px;
  overflow-y: auto;
}

.service .container {
  padding-bottom: 40px;
}

.service .service-item {
  flex: 0 0 33.3333%;
  max-width: 33.3333%;
  margin-bottom: 30px;
}

.service .service-item .service-item-inner {
  background-color: var(--bg-black-100);
  border: 1px solid var(--bg-black-50);
  text-align: center;
  border-radius: 10px;
  padding: 30px 15px;
  transition: all .3s;
}

.service .service-item .service-item-inner:hover {
  box-shadow: 0 0 20px #302e4d26;
}

.service .service-item .service-item-inner .icon {
  text-align: center;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  margin: 0 auto 30px;
  transition: all .3s;
  display: block;
}

.service .service-item .service-item-inner .icon .fa {
  color: var(--skin-color);
  font-size: 40px;
  line-height: 60px;
}

.service .service-item .service-item-inner:hover .icon {
  background: var(--skin-color);
}

.service .service-item .service-item-inner:hover .icon .fa {
  color: #fff;
  font-size: 25px;
}

.service .service-item .service-item-inner h4 {
  color: var(--text-black-900);
  text-transform: capitalize;
  margin-bottom: 15px;
  font-size: 18px;
  font-weight: 700;
}

.service .service-item .service-item-inner p {
  color: var(--text-black-700);
  font-size: 16px;
  line-height: 25px;
}

#portfolio {
  padding: 50px 0;
}

.work-list {
  flex-wrap: wrap;
  margin: 0 -15px;
  display: flex;
}

.portfolio-item {
  box-sizing: border-box;
  flex: 0 0 33.3333%;
  max-width: 33.3333%;
  margin-bottom: 30px;
  padding: 15px;
}

.portfolio-item-inner {
  cursor: pointer;
  border-radius: 10px;
  position: relative;
  overflow: hidden;
}

.portfolio-item img {
  border-radius: 10px;
  width: 100%;
  transition: transform .5s;
  display: block;
}

.portfolio-info {
  background: linear-gradient(#0009, var(--skin-color));
  color: #fff;
  text-align: center;
  width: 100%;
  height: 100%;
  padding: 20px;
  transition: transform .5s;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: translateY(100%);
}

.portfolio-item:hover img {
  transform: scale(1.1);
}

.portfolio-item:hover .portfolio-info {
  transform: translateY(0);
}

.portfolio-info h3 {
  margin-bottom: 10px;
  font-weight: 500;
}

.portfolio-heading h3 {
  color: var(--text-black-900);
  margin-bottom: 20px;
  font-size: 30px;
  font-weight: 700;
}

.portfolio-info p {
  margin-bottom: 20px;
  font-size: 14px;
}

.portfolio-info a {
  color: var(--skin-color);
  text-align: center;
  background: #fff;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  margin-top: 20px;
  font-size: 18px;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
}

.modal {
  z-index: 1100;
  background-color: #0009;
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.modal-content {
  border: 3px solid var(--skin-color);
  background-color: #fff;
  border-radius: 10px;
  width: 60%;
  max-width: 800px;
  margin: 10% auto;
  padding: 20px;
  position: relative;
  box-shadow: 0 4px 10px #00000026;
}

.modal-content .close {
  cursor: pointer;
  color: var(--skin-color);
  font-size: 28px;
  font-weight: bold;
  transition: color .3s;
  position: absolute;
  top: 10px;
  right: 20px;
}

.modal-content .close:hover, .modal-content .close:focus {
  color: #000;
}

.portfolio .container {
  padding-bottom: 40px;
}

.portfolio .portfolio-heading {
  flex: 0 0 100%;
  max-width: 100%;
  margin-bottom: 40px;
}

.portfolio .portfolio-heading h2 {
  color: var(--text-black-900);
  font-weight: 500;
}

.portfolio .portfolio-item {
  flex: 0 0 33.3333%;
  max-width: 33.3333%;
  margin-bottom: 30px;
}

.portfolio .portfolio-item-inner {
  border: 6px solid var(--bg-black-100);
  cursor: pointer;
  border-radius: 10px;
  overflow: hidden;
}

.portfolio .portfolio-item-inner .portfolio-img img {
  width: 100%;
  display: block;
}

#blog {
  padding: 50px 0;
}

.blog-heading {
  background: var(--skin-color);
  color: #fff;
  text-align: center;
  border-radius: 10px;
  width: 100%;
  padding: 50px 20px;
  font-size: 23px;
}

.blog-heading h1 {
  color: #fff;
  margin-bottom: 10px;
  font-size: 36px;
}

.blog-heading p {
  color: #fff;
  margin: 0;
  font-size: 18px;
}

#blog .portfolio-heading {
  text-align: center;
  flex: 0 0 100%;
  max-width: 100%;
  margin-bottom: 40px;
}

#blog .portfolio-heading h3 {
  color: var(--text-black-900);
  margin-bottom: 10px;
  font-size: 30px;
  font-weight: 700;
}

#blog .portfolio-heading p {
  color: var(--text-black-700);
  margin-top: 0;
  font-size: 16px;
}

#blog .work-list {
  flex-wrap: wrap;
  margin: 0 -15px;
  display: flex;
}

#blog .portfolio-item {
  box-sizing: border-box;
  flex: 0 0 33.3333%;
  max-width: 33.3333%;
  margin-bottom: 30px;
  padding: 15px;
  display: flex;
}

#blog .portfolio-item-inner {
  border: 6px solid var(--bg-black-100);
  background: #fff;
  border-radius: 10px;
  flex-direction: column;
  width: 100%;
  height: 100%;
  transition: all .3s ease-in-out;
  display: flex;
  position: relative;
  overflow: hidden;
}

#blog .portfolio-img {
  height: 200px;
  overflow: hidden;
}

#blog .portfolio-img img {
  object-fit: cover;
  border-radius: 10px 10px 0 0;
  width: 100%;
  height: 100%;
  transition: transform .5s;
  display: block;
}

#blog .portfolio-item:hover img {
  transform: scale(1.05);
}

#blog .portfolio-info {
  background: linear-gradient(#0006, var(--skin-color));
  color: #fff;
  text-align: center;
  flex-direction: column;
  flex-grow: 1;
  justify-content: space-between;
  padding: 20px;
  display: flex;
}

#blog .portfolio-info h3 {
  margin-bottom: 10px;
  font-size: 20px;
  font-weight: 600;
}

#blog .portfolio-info p {
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  margin-bottom: 20px;
  font-size: 14px;
  line-height: 1.4;
  display: -webkit-box;
  overflow: hidden;
}

#blog .btn.read-more {
  color: var(--skin-color);
  cursor: pointer;
  background: #fff;
  border: none;
  border-radius: 50px;
  padding: 10px 20px;
  font-size: 14px;
  font-weight: bold;
  text-decoration: none;
  transition: all .3s ease-in-out;
}

#blog .btn.read-more:hover {
  background: var(--skin-color);
  color: #fff;
}

@media (width <= 991px) {
  #blog .portfolio-item {
    flex: 0 0 50%;
    max-width: 50%;
  }

  .blog-header h1 {
    font-size: 30px;
  }

  .blog-header p {
    font-size: 16px;
  }
}

@media (width <= 767px) {
  #blog .portfolio-item {
    flex: 0 0 100%;
    max-width: 100%;
  }

  #blog .portfolio-heading h3, .blog-header h1 {
    font-size: 24px;
  }

  #blog .portfolio-img {
    height: 180px;
  }

  #blog .portfolio-info h3 {
    font-size: 18px;
  }

  #blog .portfolio-info p {
    font-size: 13px;
  }
}

.contact-title {
  color: var(--skin-color);
  text-align: center;
  margin-bottom: 20px;
  font-size: 25px;
}

.contact-subtitle {
  color: var(--text-black-900);
  text-align: center;
  margin-bottom: 60px;
  font-size: 15px;
}

.contact .contact-info-item {
  text-align: center;
  flex: 0 0 33.3333%;
  max-width: 33.3333%;
  margin-bottom: 60px;
}

.contact .contact-info-item .icon {
  display: inline-block;
}

.contact .contact-info-item .icon .fa {
  color: var(--skin-color);
  font-size: 25px;
}

.contact .contact-info-item h4 {
  color: var(--text-black-900);
  text-transform: capitalize;
  margin: 15px 0 5px;
  font-size: 18px;
  font-weight: 700;
}

.contact .contact-info-item p {
  color: var(--text-black-700);
  font-size: 16px;
  font-weight: 400;
  line-height: 25px;
}

.contact .contact-form {
  flex: 0 0 100%;
  max-width: 100%;
}

.contact .contact-form .col-6 {
  flex: 0 0 50%;
  max-width: 50%;
}

.contact .contact-form .col-12 {
  flex: 0 0 100%;
  max-width: 100%;
}

.contact .contact-form .form-item {
  margin-bottom: 30px;
}

.contact .contact-form .form-item .form-control {
  border: 1px solid var(--bg-black-50);
  background-color: var(--bg-black-100);
  width: 100%;
  height: 50px;
  color: var(--text-black-700);
  border-radius: 15px;
  padding: 10px 25px;
  font-size: 16px;
  transition: all .3s;
}

.contact .contact-form .form-item .form-control:focus {
  box-shadow: 0 0 20px #302e4d26;
}

.contact .contact-form .form-item textarea.form-control {
  height: 150px;
}

.contact .contact-form .btn {
  height: 50px;
  padding: 0 50px;
}

footer.footer.section {
  background-color: var(--skin-color);
  color: #eee;
  text-align: center;
  padding: 40px 20px 20px;
  font-family: Poppins, sans-serif;
}

.footer .container {
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  align-items: start;
  gap: 30px;
  display: grid;
}

.footer .footer-logo h2 {
  color: #fff;
  margin-bottom: 10px;
  font-size: 32px;
  font-weight: 700;
}

.footer .footer-logo p {
  color: #ccc;
  margin: 0;
  font-size: 14px;
}

.footer .social-icon {
  justify-content: center;
  gap: 15px;
  margin: 20px 0;
  padding: 0;
